import React, { Component, PropTypes } from 'react';

class NoResult extends Component {
  constructor(props) {
    super(props);
    this.state = {
      search: props.search,
    };
  }

  render() {
    const { search } = this.state;
    return (
      <div className="a-left p4">
        <div className="py2" style={{ fontSize: '70px' }}>
          :(
        </div>
        <div className="fz16 mb3">
               
          <strong>
            {search !== '' ? ` "${search}"` : null}
          </strong>
          ,   !!
        </div>
        <p>
            <br />
          ,   <br />
          <span className="corect-line">
            <b className="txt-danger"></b>{'  '}
            <b className="txt-primary"></b><br />
          </span>
             <br />
              
        </p>
        <img src="/images/blue_arrow_back_horizontal.png" />
      </div>
    );
  }
}

NoResult.propTypes = {
  search: PropTypes.string,
};

export default NoResult;



